.Main {
  height: 100vh;
  width: 100vw;
  display: flex;
  background-image: url("../images/background.jpg");
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.Sidebar-container {
  width: 25%;
  max-width: 250px;
  margin-top: 75px;
  margin-bottom: 75px;
}

.Page {
  margin: 75px;
  padding: 50px;
  background: whitesmoke;
  width: 100%;
  border: 5px solid black;
  border-radius: 10px;
}
